.main-table {
  display: flex;
  flex-direction: column;
  min-height: 300px;
  overflow: hidden;
}

.main-table h2 {
  font-size: 1.8em;
  font-weight: bold;
  padding: 15px 30px;
}

.table {
  flex: 1;
  overflow-y: auto;
  border-top: 1px solid #eee;
}

.table-header,
.table-row {
  display: flex;
  background: #fff;
  border-bottom: 1px solid #eee;
}

.table-header {
  position: sticky;
  top: 0;
}

.table-row {
  cursor: pointer;
}

.table-row:nth-child(even) {
  background: #f5f5f5;
}

.table-row-active {
  color: #fff;
  background: #ef5350 !important;
  border-color: #ef5350;
}

.table-header-item,
.table-row-item {
  width: calc(100% / 4);
  padding: 10px 30px;
}

.table-header-item {
  font-size: 1.2em;
  font-weight: bold;
}
