.main-map {
  height: 100%;
  background: #999;
}

.map-marker {
  position: relative;
  width: 0;
  height: 0;
  background: red;
  text-align: center;
}

.map-marker .pin {
  position: absolute;
  width: 26px;
  height: 26px;
  top: calc(-26px + 100% / 2);
  left: calc(100% / 2 - 26px / 2);
  transform: rotateX(20deg) rotateZ(-45deg);
  border-radius: 50% 50% 50% 0;
  background: #039be5;
  -webkit-box-shadow: -1px 1px 4px rgba(0, 0, 0, 0.5);
  -moz-box-shadow: -1px 1px 4px rgba(0, 0, 0, 0.5);
  box-shadow: -1px 1px 4px rgba(0, 0, 0, 0.5);
}

.map-marker p {
  position: absolute;
  width: 26px;
  height: 26px;
  top: calc(-26px + 100% / 2);
  left: calc(100% / 2 - 26px / 2);
  color: #fff;
  line-height: 26px;
}

.map-marker-active .pin {
  background: #ef5350;
}

/* OpenLayers re-style */

.ol-zoom {
  top: 30px;
  left: unset;
  right: 0;
}

.ol-control {
  padding: 0;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 3px 0 0 3px;
}

.ol-control:hover {
  background-color: rgba(0, 0, 0, 0.5);
}

.ol-zoom button {
  font-size: 28px;
  margin: 0;
  background-color: unset;
}

.ol-zoom button:hover,
.ol-zoom button:focus:hover {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.5);
}

.ol-zoom button:focus {
  background-color: unset;
  outline: none;
}
