html,
body {
  height: 100%;
}

.button {
  padding: 10px;
  text-align: center;
  color: #fff;
  background: #039be5;
  border: none;
  border-radius: 3px;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.35);
}

.button:disabled {
  background: #ddd;
}

.button:hover:not(:disabled) {
  cursor: pointer;
  background: #0288d1;
}

.app {
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
}

.header {
  cursor: pointer;
  position: relative;
  padding: 15px 30px;
  background: #fff;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.35);
  z-index: 9999;
}

.header h1 {
  font-size: 2em;
  font-weight: bold;
  line-height: 100%;
}

.container {
  flex: 1;
  display: flex;
  overflow: hidden;
}

.container > div {
  flex: 1;
}

.side {
  max-width: 300px;
  padding: 30px;
  background: #f5f5f5;
  border-right: 1px solid #ddd;
}

.side button {
  max-width: 150px;
  width: 100%;
}

.main {
  display: flex;
  flex-direction: column;
  width: 100%;
  overflow: hidden;
}
